
@charset "UTF-8";
/* スマホ */
@media screen and (min-width: 0px) {
  /*  全体　*/
#app {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    width: auto;
}
.validation {
    display: none;
}
.fa-crown {
    color: #b43ae2;
}
.wrapper {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}
.point {
    cursor: pointer;
}
* {
    margin: 0px;
    padding: 0px;
}
a {
    display: inline-block;
}
  /* ヘッダー */
header .wf-nicomoji {
    font-family: "Nico Moji";
    font-size: 75px;
    color: #b43ae2;
}
header p {
    margin: 0px;
}
header #search {
    background: #b43ae2;
}
header #search_btn {
    background-color: white;
    font-size: 90%;
    margin: 6px;
    padding: 3px 6px;
    border-radius: 10px;
}
  /* ランキングのとこ */
.rank {
    width: 90%;
    margin: 30px auto;
    border-radius: 10px;
    border: 1px #b43ae2;
}
.rank .rank-top {
      position: relative;
      padding: 0 20px;
      margin: 10px;
}
.rank .rank-top:after {
        content: "";
        display: block;
        height: 2px;
        background: -webkit-gradient(linear, left top, right top, from(#f6cbff), to(#b43ae2));
        background: linear-gradient(to right, #f6cbff, #b43ae2);
}
.rank a {
      position: relative;
      padding: 0 20px;
      margin: 10px;
}
.rank .wf-nicomoji {
      font-family: "Nico Moji";
      font-size: 33px;
      background-color: #b43ae2;
      border-top-left-radius: 9px;
      border-top-right-radius: 9px;
      color: white;
      margin: 0;
      padding: 5px;
}
.rank img {
      width: 40%;
      height: auto;
      margin-bottom: 10px;
}
.rank .btn {
      text-decoration: none;
      margin-top: 20px;
      color: white;
      font-weight: bold;
      background-color: #b43ae2;
      border-radius: 30px;
      padding: 6px;
}
.rank .ppl {
      background-color: rgba(215, 47, 252, 0.1);
}
.rank .hide_rank {
      display: none;
}
  /* フッター */
footer {
    height: 50px;
    background-color: #b43ae2;
    bottom: 0;
}
.modal {
    position: fixed;
    background-color: white;
    width: 80%;
    margin: 0px auto;
    border: 1px solid #b43ae2;
    border-radius: 10px;
    z-index: 100;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.modal .modal-contents {
      margin: 10px;
}
.modal p {
      font-size: 120%;
      border-top-left-radius: 9px;
      border-top-right-radius: 9px;
      background-color: #b43ae2;
      padding: 5px;
      font-weight: bold;
      color: white;
}
.modal img {
      width: 60%;
      height: auto;
      margin-bottom: 10px;
}
.modal #line {
      position: relative;
      padding: 0 20px;
      margin: 10px;
}
.modal #line:after {
        content: "";
        display: block;
        height: 2px;
        background: -webkit-gradient(linear, left top, right top, from(#f6cbff), to(#b43ae2));
        background: linear-gradient(to right, #f6cbff, #b43ae2);
}
.modal .fa-handshake {
      color: #b43ae2;
}
#close {
    color: #b43ae2;
    font-weight: bold;
}
}
/* PC */
@media screen and (min-width: 481px) {
header p {
    font-size: 150%;
}
header input {
    font-size: 100%;
}
.rank {
    margin: 30px auto;
    border-radius: 10px;
    border: 1px solid #b43ae2;
    width: 50%;
}
.rank a {
      position: relative;
      padding: 0 20px;
      margin: 10px;
      font-size: 20px;
}
.rank img {
      width: 20%;
      height: auto;
      margin-bottom: 10px;
}
.modal {
    position: fixed;
    background-color: white;
    width: 60%;
    margin: 0px auto;
    border-radius: 10px;
    z-index: 100;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.modal img {
      width: 30%;
      height: auto;
      margin-bottom: 10px;
}
}

